.dark-mode{
    background-color: darken($grisOscuro,10);
    p{
        color: $blanco;
    }
    h1,h2,h3,h4,blockquote{
        color: $gris;
    }
    .anuncio,
    .resumen-propiedad{
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;
        .precio{
            color: $verde;
        }
        .icono{
            filter: invert(100%);
        }
    }
    .formulario{
        label,
        legend,
        p{
            color: $gris;
        }
        select,
        textarea,
        input:not([type="submit"]){
        background-color: $grisOscuro;
        color: $gris;
        border: 1px solid $grisOscuro;
    }
    }
    td{
        color: $blanco;
    }
}