.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablet{
        align-items: flex-end;
    }

    .dark-mode-boton{
        max-width: 3rem;
        margin-top: 3rem;
        margin-bottom: 2rem;
        filter:invert(100%);
        &:hover{
            cursor: pointer;
        }
        @include tablet{
            margin-top: 0;
        }
    }
}
.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;
    padding-bottom: 3rem;
    @include tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
        /* img{
            width: 30rem;
            height: auto;
        } */
        h1{
            text-transform: uppercase;
            font-family: $fuenteLogoSec;
            color: $blanco;
            margin: 0;
            span{
                font-family: $fuenteLogo;
                font-size: 4.1rem;
            }
        }
    }
}

.navegacion {
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .5s ease-in-out, opacity 0.5s linear, height 0.5s linear;
    @include tablet{
        display:flex ;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }

    a{
        color:$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include tablet{
            margin-right: 2rem;
            font-size: 1.8rem;
            &:last-of-type{
                margin-right: 0;
            }
        }
    }
    a:hover{
        color: $verde;
    }
    
}
//Menu de hamburguesa
.mobile-menu{
    @include tablet{
        display: none;
    }

    img{
        width: 5rem;
    }
}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
}